/**
 * External dependencies
 */
import { HeaderIcon } from '~cartzillagb/icons'
import { disabledBlocks, i18n } from 'cartzillagb'

import {
	createButtonAttributes,
	createResponsiveAttributes,
	createAllCombinationAttributes,
	getAttrName
} from '~cartzillagb/util'


/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n'
import { applyFilters } from '@wordpress/hooks'

import {
	withBlockStyles,
	withUniqueClass,
} from '~cartzillagb/higher-order'


import {
	BlockContainer,
	ButtonEditHelper
} from '~cartzillagb/components'

import classnames from 'classnames'
import createStyles from './style'

/**
 * WordPress dependencies
 */
import { Fragment } from '@wordpress/element'
import {
	InnerBlocks,
	RichText
} from '@wordpress/block-editor'
import { compose } from '@wordpress/compose'

const schema_1 = {
	blockId: {
		type: 'string',
	},

	enableContainer: {
		type: 'boolean',
		default: true,
	},

	enableHeader: {
		type: 'boolean',
		default: true,
	},

	headerStyle: {
		type: 'string',
		default: 'style-v1',
	},

	// Title.
	showTitle: {
		type: 'boolean',
		default: true,
	},

	title: {
		type: 'string',
		default: __( 'Title for This Block', i18n ),
	},

	// Header Button.
	showHeaderButton: {
		type: 'boolean',
		default: false,
	},

	...createButtonAttributes( 'headerButton%s', {
		defaultText: 'More products',
		defaultIcon: 'czi-arrow-right',
		defaultSize: 'sm',
		defaultDesign: 'outline',
		backgroundColor: 'accent',
		iconAfterText: true,
	} ),

	// Footer Button.
	showFooterButton: {
		type: 'boolean',
		default: true,
	},

	...createButtonAttributes( 'footerButton%s', {
		defaultText: 'More products',
		defaultIcon: 'czi-arrow-right',
		defaultDesign: 'outline',
		backgroundColor: 'accent',
		iconAfterText: true,
	} ),

	// Spacing.
	...createResponsiveAttributes( 'header%sBottomMargin', {
		type: 'number',
		default: '',
	} ),

	...createResponsiveAttributes( 'footer%sTopMargin', {
		type: 'number',
		default: '',
	} ),

	// Carousel Options
	...createAllCombinationAttributes(
		'slidesToShow%s', {
			type: 'number',
		},
		[ '', 'Laptop', 'Tablet', 'Mobile' ]
	),

	...createAllCombinationAttributes(
		'slidesToScroll%s', {
			type: 'number',
		},
		[ '', 'Laptop', 'Tablet', 'Mobile' ]
	),

	dots: {
		type: 'boolean',
		default: true,
	},

	arrows: {
		type: 'boolean',
		default: true,
	},

	infinite: {
		type: 'boolean',
		default: true,
	},
}

const save_1 = props => {
	const {
		className,
		attributes,
	} = props

	const {
		blockId,
		enableContainer,

		// Title.
		showTitle = true,
		title = '',

		// Button.
		showHeaderButton = false,
		headerButtonText,

		showFooterButton = false,
		footerButtonText,

		// Carousel
		arrows,
		infinite,
	} = attributes

	const getAttrValue = ( attrName, defaultValue = '' ) => {
		const value = props.attributes[ getAttrName( attrName ) ]
		return value === 0 ? value : ( value || defaultValue )
	}

	const mainClasses = classnames( [
		'czgb-products-carousel-block',
		className,
		enableContainer ? 'container' : '',
	], applyFilters( 'cartzillagb.products-carousel.main-block.classes', {}, props ) )

	const titleComp = showTitle &&
		<RichText.Content
			tagName={ 'h3' }
			value={ title }
			className={ classnames( [
				'czgb-products-carousel__title',
				showHeaderButton ? 'h3 mb-0 pt-3 mr-2' : 'h3 text-center',
			] ) }
			
		/>

	const headerButtonComp = showHeaderButton && ! RichText.isEmpty( headerButtonText ) &&
		<ButtonEditHelper.Content
			attrNameTemplate={ `headerButton%s` }
			blockAttributes={ props.attributes }
		/>

	const footerButtonComp = showFooterButton && ! RichText.isEmpty( footerButtonText ) &&
		<ButtonEditHelper.Content
			attrNameTemplate={ `footerButton%s` }
			blockAttributes={ props.attributes }
		/>

	const headerComp = showTitle && showHeaderButton ? (
		<div className="czgb-products-carousel__header d-flex flex-wrap justify-content-between align-items-center pt-1 border-bottom pb-4 mb-4" >
			{ titleComp }
			<div className="pt-3" >
				{ headerButtonComp }
			</div>
		</div>
	) : ( showTitle ? (
		titleComp
	) : ( showHeaderButton ? (
		<div className="czgb-products-carousel__header d-flex flex-wrap justify-content-between align-items-center pt-1 border-bottom pb-4 mb-4" >
			{ headerButtonComp }
		</div>
	) : '' ) )

	const footerComp = showFooterButton && (
		<div className="czgb-products-carousel__footer text-center pt-3" >
			{ footerButtonComp }
		</div>
	)

	const defaultCarouselOptions = applyFilters( 'cartzillagb.products-carousel.main-block.classes', {
		slidesToShow: 5,
		slidesToScroll: 5,
		dots : true,
		arrows : true,
		adaptiveHeight : true,
		prevArrow: '<i class="czi-arrow-left"></i>',
		nextArrow: '<i class="czi-arrow-right"></i>',
		customArrows: '.cz-custom-controls',
		speed: 500,
		pauseOnHover: true,
		responsive: [
			{
				breakpoint: 1198.98,
				settings: {
					slidesToShow: 2,
					slidesToScroll: 2
				}
			},
			{
				breakpoint: 991.98,
				settings: {
					slidesToShow: 3,
					slidesToScroll: 3
				}
			},
			{
				breakpoint: 575.98,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1
				}
			}
		],
	}, props )

	let carouselOptions = {
		slidesToShow: getAttrValue( 'slidesToShow', 5 ),
		slidesToScroll: getAttrValue( 'slidesToScroll', 5 ),
		dots: getAttrValue( 'dots' ),
		arrows: getAttrValue( 'arrows' ),
		responsive: [
			{
				breakpoint: 1198.98,
				settings: {
					slidesToShow: getAttrValue( 'slidesToShowLaptop', 4 ),
					slidesToScroll: getAttrValue( 'slidesToScrollLaptop', 4 ),
				}
			},
			{
				breakpoint: 991.98,
				settings: {
					slidesToShow: getAttrValue( 'slidesToShowTablet', 3 ),
					slidesToScroll: getAttrValue( 'slidesToScrollTablet', 3 ),
				}
			},
			{
				breakpoint: 575.98,
				settings: {
					slidesToShow: getAttrValue( 'slidesToShowMobile', 1 ),
					slidesToScroll: getAttrValue( 'slidesToScrollMobile', 1 ),
				}
			}
		],
	}

	const blockAttributes = { ...attributes }

	if( infinite === false ) {
		carouselOptions.infinite = false
	} else {
		delete blockAttributes.infinite
	}

	carouselOptions = { ...defaultCarouselOptions, ...carouselOptions }

	const wraperAttr = {}

	wraperAttr.className = classnames( [
		'cz-carousel',
		arrows && 'cz-controls-outside',
		showHeaderButton ? 'pt-2' : 'pt-4',
	] )

	return (
		<BlockContainer.Save className={ mainClasses } blockProps={ { ...props, attributes:{ ...blockAttributes, anchor: blockId } } } data-slick-classes="js-slick-carousel" data-slick-attributes={ JSON.stringify( carouselOptions ) } render={ () => (
			<Fragment>
				{ headerComp }
				<div { ...wraperAttr }>
					<div className="cz-custom-controls"></div>
					<InnerBlocks.Content />
				</div>
				{ footerComp }
			</Fragment>
		) } />
	)
}


const deprecated = [
	{
		attributes: schema_1,
		save: save_1,
	},
]

export default deprecated